html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif; }

* {
  box-sizing: border-box; }

ul li {
  list-style: none; }

a {
  text-decoration: none; }

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

.button-outlined {
  align-items: center;
  background-color: transparent;
  -webkit-appearance: none;
  vertical-align: middle;
  justify-content: center;
  border: 1px solid #eee; }

.button-blue-line {
  border: 2px solid #3d5b99;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #3d5b99;
  background: #fff;
  border-radius: 3px;
  font-family: 'Lato', sans-serif;
  justify-content: center;
  align-items: center; }

.button-red-solid {
  justify-content: center;
  align-items: center;
  border: 0;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  font-family: 'Lato', sans-serif;
  border: 3px solid #db443a;
  background: #db443a;
  border-radius: 3px;
  text-transform: uppercase; }

.button-blue-solid {
  border: 3px solid #3d5b99;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  background: #3d5b99;
  border-radius: 3px;
  font-family: 'Lato', sans-serif;
  justify-content: center;
  align-items: center;
  text-transform: uppercase; }
  .button-blue-solid:hover {
    background-color: #3f61a9;
    border-color: #3f61a9; }

@media (min-width: 992px) {
  .container {
    max-width: 960px; } }

@media (min-width: 1200px) {
  .container {
    max-width: 1450px; } }

.tabs {
  background: #3d5b99;
  display: block; }
  .tabs .tabs-main {
    overflow: visible;
    position: relative; }
    .tabs .tabs-main .tab-list {
      margin-top: -62px; }
      .tabs .tabs-main .tab-list .tabs-button {
        margin-right: 1px;
        padding: 15px 30px;
        background: #5774af;
        min-height: 60px;
        border: 0;
        color: #fff;
        font-size: 18px;
        font-family: "Lato", sans-serif;
        text-transform: capitalize;
        border-top: 5px solid transparent;
        cursor: pointer; }
        .tabs .tabs-main .tab-list .tabs-button.tab-list-active {
          background: #3d5b99;
          border-top: 5px solid #db443a; }

.form-wrapper {
  display: flex;
  width: 100%; }
  .form-wrapper .button-fit-search {
    background: #db443a;
    color: #fff;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    font-weight: bold;
    border: 0;
    width: 101%;
    height: 66px;
    text-transform: uppercase;
    border-radius: 0 4px 4px 0;
    cursor: pointer; }
    .form-wrapper .button-fit-search.button-vin-search {
      flex-basis: 230px;
      width: 230px; }
  .form-wrapper form {
    width: 100%; }
  .form-wrapper .input-wrapper {
    display: flex; }

.fitment-input {
  position: relative;
  flex: 1 0 0;
  background: #fff;
  border-right: 1px solid #a8a8a8; }
  .fitment-input:first-child {
    border-radius: 3px 0 0 3px; }
  .fitment-input:last-child {
    border-radius: 0 3px 3px 0; }
  .fitment-input input[type='text'] {
    width: 100%;
    padding: 20px;
    border: 0;
    outline: 0;
    border-radius: 4px 0 0 4px;
    line-height: 26px;
    font-size: 15px; }
  .fitment-input .select-text {
    height: 66px;
    width: 100%;
    background: transparent;
    border: 0;
    position: relative;
    font-size: 18px;
    padding-left: 15px;
    font-family: 'Lato', sans-serif;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    cursor: default; }
    .fitment-input .select-text span.meta {
      font-family: 'Lato', sans-serif;
      font-size: 14px; }
    .fitment-input .select-text span.right-arrow {
      position: absolute;
      right: 5px; }
  .fitment-input .dropdown {
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    background: #fff;
    border: 1px solid #ccc;
    top: 100%; }
    .fitment-input .dropdown ul {
      padding: 0;
      margin: 0; }
      .fitment-input .dropdown ul li {
        padding: 0 15px;
        line-height: 35px; }
        .fitment-input .dropdown ul li:hover {
          background-color: #3d5b99;
          color: #fff; }

.fitment-input-button-container {
  display: flex; }
