html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif; }

* {
  box-sizing: border-box; }

ul li {
  list-style: none; }

a {
  text-decoration: none; }

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

.button-outlined {
  align-items: center;
  background-color: transparent;
  -webkit-appearance: none;
  vertical-align: middle;
  justify-content: center;
  border: 1px solid #eee; }

.button-blue-line {
  border: 2px solid #3d5b99;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #3d5b99;
  background: #fff;
  border-radius: 3px;
  font-family: 'Lato', sans-serif;
  justify-content: center;
  align-items: center; }

.button-red-solid {
  justify-content: center;
  align-items: center;
  border: 0;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  font-family: 'Lato', sans-serif;
  border: 3px solid #db443a;
  background: #db443a;
  border-radius: 3px;
  text-transform: uppercase; }

.button-blue-solid {
  border: 3px solid #3d5b99;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  background: #3d5b99;
  border-radius: 3px;
  font-family: 'Lato', sans-serif;
  justify-content: center;
  align-items: center;
  text-transform: uppercase; }
  .button-blue-solid:hover {
    background-color: #3f61a9;
    border-color: #3f61a9; }

@media (min-width: 992px) {
  .container {
    max-width: 960px; } }

@media (min-width: 1200px) {
  .container {
    max-width: 1450px; } }

.widget {
  width: 100%;
  background: #fff;
  padding: 15px 30px 30px;
  border-radius: 3px;
  min-height: 300px;
  margin-bottom: 30px;
  -webkit-box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.1); }
  .widget .widget-header {
    font-size: 26px;
    font-family: 'Lato', sans-serif;
    font-weight: 600;
    width: 100%;
    text-align: center;
    color: #222;
    text-transform: capitalize; }
  .widget .slick-prev,
  .widget .slick-next {
    position: absolute;
    bottom: 50%;
    text-indent: -999999px;
    border: 0;
    width: 40px;
    height: 70px;
    cursor: pointer; }
    .widget .slick-prev:focus,
    .widget .slick-next:focus {
      outline: none; }
  .widget .slick-prev {
    background: url(/public/images/arrow-left.png) no-repeat center center;
    left: -30px; }
  .widget .slick-next {
    background: url(/public/images/arrow-right.png) no-repeat center center;
    right: -30px; }

.featured-category-container {
  display: flex;
  width: 100%; }

.categoty-box {
  border: 1px solid #e1e1e1;
  min-height: 200px;
  border-radius: 3px;
  flex: 1 0 0;
  margin: 15px;
  display: flex; }
  .categoty-box:first-child {
    margin-left: 0; }
  .categoty-box .cb-inner {
    display: flex;
    width: 100%;
    height: 200px;
    justify-content: center;
    flex: 1;
    color: #222;
    overflow: hidden;
    flex-wrap: wrap; }
    .categoty-box .cb-inner img {
      display: flex;
      height: 76%;
      transition: height 0.5s ease; }
    .categoty-box .cb-inner p {
      display: flex;
      align-items: center;
      align-content: center;
      justify-content: center;
      width: 100%;
      margin-left: 10px;
      margin-right: 10px;
      margin-bottom: 20px;
      border-bottom: 1px solid transparent;
      transition: all 0.5s ease; }
    .categoty-box .cb-inner ul {
      margin: 0;
      padding: 0;
      width: 100%; }
      .categoty-box .cb-inner ul li {
        list-style: none;
        padding: 0 10px; }
        .categoty-box .cb-inner ul li a {
          font-size: 14px;
          line-height: 24px;
          color: #333;
          display: inline; }
          .categoty-box .cb-inner ul li a:hover {
            text-decoration: underline; }
    .categoty-box .cb-inner.open img {
      height: 35%;
      transition: height 0.5s ease; }
    .categoty-box .cb-inner.open p {
      font-weight: 700;
      color: #3d5b99;
      margin-bottom: 0px;
      border-bottom: 1px solid #eee;
      transition: all 0.5s ease; }

.product-card {
  border: 1px solid transparent;
  padding: 15px;
  border-radius: 3px; }
  .product-card .product-img {
    width: 100%;
    margin-bottom: 20px; }
    .product-card .product-img img {
      max-width: 224px;
      width: 100%; }
  .product-card .product-body h3 {
    color: #333;
    font-size: 16px;
    font-weight: 700;
    text-align: left;
    padding-right: 40px; }
  .product-card .product-body p {
    color: #db443a;
    font-size: 20px;
    font-weight: 400;
    margin: 0;
    padding: 0; }
  .product-card .product-body a {
    font-size: 14px;
    color: #3d5b99;
    font-weight: 700; }
  .product-card:hover {
    border: 1px solid #ccc; }

.diagramWidget {
  width: 100%;
  background: #fff;
  padding: 15px 30px 30px;
  border-radius: 3px;
  min-height: 300px;
  margin-bottom: 30px;
  -webkit-box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.1);
  display: flex;
  background: #244ca0 url(/public/images/diagram-bg.png) no-repeat center center; }
  .diagramWidget .diagramWidget-content {
    margin-left: auto;
    width: 700px;
    text-align: center;
    align-items: center;
    align-content: center;
    justify-content: center; }
    .diagramWidget .diagramWidget-content h2 {
      font-size: 48px;
      margin-bottom: 0;
      color: #fff;
      margin-top: 42px; }
    .diagramWidget .diagramWidget-content p {
      font-size: 18px;
      margin: 0 0 20px 0;
      color: #fff; }
    .diagramWidget .diagramWidget-content a {
      background: #fff;
      color: #db443a;
      font-size: 20px;
      border-radius: 3px;
      padding: 17px 50px;
      padding: 17px 50px;
      display: inline-block; }
      .diagramWidget .diagramWidget-content a:hover {
        text-decoration: underline; }
