html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif; }

* {
  box-sizing: border-box; }

ul li {
  list-style: none; }

a {
  text-decoration: none; }

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

.button-outlined {
  align-items: center;
  background-color: transparent;
  -webkit-appearance: none;
  vertical-align: middle;
  justify-content: center;
  border: 1px solid #eee; }

.button-blue-line {
  border: 2px solid #3d5b99;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #3d5b99;
  background: #fff;
  border-radius: 3px;
  font-family: 'Lato', sans-serif;
  justify-content: center;
  align-items: center; }

.button-red-solid {
  justify-content: center;
  align-items: center;
  border: 0;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  font-family: 'Lato', sans-serif;
  border: 3px solid #db443a;
  background: #db443a;
  border-radius: 3px;
  text-transform: uppercase; }

.button-blue-solid {
  border: 3px solid #3d5b99;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  background: #3d5b99;
  border-radius: 3px;
  font-family: 'Lato', sans-serif;
  justify-content: center;
  align-items: center;
  text-transform: uppercase; }
  .button-blue-solid:hover {
    background-color: #3f61a9;
    border-color: #3f61a9; }

@media (min-width: 992px) {
  .container {
    max-width: 960px; } }

@media (min-width: 1200px) {
  .container {
    max-width: 1450px; } }

.react-autosuggest__container {
  position: relative; }

.react-autosuggest__input {
  width: 100%;
  height: 45px;
  padding: 0 65px 0 15px;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaa;
  border-radius: 4px;
  font-family: 'Lato', sans-serif; }

.react-autosuggest__input--focused {
  outline: none; }

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0; }

.react-autosuggest__suggestions-container {
  display: none; }

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 100%;
  width: calc(100% - 50px);
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2; }

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px; }

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd; }

.head-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  width: 100%;
  position: fixed;
  z-index: 2;
  border-bottom: 1px solid #c0c0c0; }

header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: flex-start;
  align-items: center;
  height: 80px;
  max-width: 1580px;
  flex-grow: 1; }
  header .logo {
    flex-basis: 182px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 30px; }
    header .logo a {
      display: inline-block; }
      header .logo a span {
        display: block;
        margin-top: 5px;
        width: 182px; }
  header .category {
    flex-basis: 160px;
    display: flex;
    align-items: center;
    justify-content: center; }
    header .category .dropdown {
      position: relative; }
      header .category .dropdown button {
        padding: 8px 5px;
        display: inline-flex;
        padding-right: 24px;
        border-radius: 4px; }
        header .category .dropdown button:focus {
          outline: none;
          border-color: #000; }
        header .category .dropdown button span {
          font-family: 'Lato', sans-serif; }
          header .category .dropdown button span.label_wrapper {
            display: flex;
            align-items: flex-start;
            flex-direction: column; }
            header .category .dropdown button span.label_wrapper .lbl1 {
              font-size: 13px;
              color: #777;
              text-transform: capitalize; }
            header .category .dropdown button span.label_wrapper .lbl2 {
              font-size: 14px;
              line-height: 14px;
              color: #555;
              text-transform: capitalize; }
        header .category .dropdown button:after {
          position: absolute;
          top: 22px;
          right: 5px;
          border-top-color: #777;
          display: inline-block;
          width: 0;
          height: 0;
          margin-left: 0.255em;
          vertical-align: 0.255em;
          content: '';
          border-top: 0.3em solid;
          border-right: 0.3em solid transparent;
          border-bottom: 0;
          border-left: 0.3em solid transparent; }

.searchbar {
  flex: 1; }
  .searchbar .form-group-wrapper {
    position: relative; }
    .searchbar .form-group-wrapper .search-icon {
      position: absolute;
      top: 0;
      bottom: 0;
      background-color: #db443a;
      right: 0;
      border-radius: 0 3px 3px 0; }
      .searchbar .form-group-wrapper .search-icon button {
        color: #fff;
        cursor: pointer;
        display: block;
        width: 50px;
        height: 45px;
        background: #db443a;
        outline: 0;
        border: 0;
        margin: 0;
        padding: 0;
        border-radius: 0 3px 3px 0;
        display: flex;
        align-items: center; }
        .searchbar .form-group-wrapper .search-icon button span {
          width: 24px;
          height: 24px;
          display: block;
          margin: 0 auto; }

.cart-buttons {
  flex-basis: 450px;
  height: 80px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  padding-left: 40px; }
  .cart-buttons .buttons-list {
    width: 100%; }
    .cart-buttons .buttons-list > ul {
      display: flex;
      flex-wrap: nowrap;
      height: 80px;
      padding: 0; }
      .cart-buttons .buttons-list > ul li {
        flex: 1;
        display: flex;
        align-items: stretch;
        justify-content: center;
        border-left: 1px solid #e1e1e1;
        position: relative;
        cursor: pointer; }
        .cart-buttons .buttons-list > ul li div.wrap {
          display: flex;
          align-items: center;
          width: 100%;
          justify-content: center; }
        .cart-buttons .buttons-list > ul li:hover > span {
          opacity: 1; }
        .cart-buttons .buttons-list > ul li > span {
          opacity: 0.4; }
        .cart-buttons .buttons-list > ul li:first-child {
          border-left: 0px; }
        .cart-buttons .buttons-list > ul li.full-cart .nav-icon {
          width: 33px;
          height: 25px; }
        .cart-buttons .buttons-list > ul li.full-cart .meta {
          padding-right: 0px;
          padding-left: 10px; }
        .cart-buttons .buttons-list > ul li.chat-button .nav-icon {
          width: 43px;
          height: 25px; }
        .cart-buttons .buttons-list > ul li.track-button .nav-icon {
          width: 32px;
          height: 25px; }
        .cart-buttons .buttons-list > ul li.help-button .nav-icon {
          width: 25px;
          height: 25px; }
