.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }

.col-auto {
  flex-basis: 0;
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  max-width: 100%; }

footer .footer-container {
  padding: 50px 0;
  background: #333; }
  footer .footer-container h4 {
    font-size: 16px;
    font-weight: 700;
    color: #222;
    text-transform: uppercase;
    padding-bottom: 10px;
    color: #eee; }
  footer .footer-container ul {
    margin: 0;
    padding: 0; }
    footer .footer-container ul li {
      display: block; }
      footer .footer-container ul li a {
        display: block;
        font-size: 14px;
        line-height: 27px;
        font-weight: 400;
        color: #ddd; }
        footer .footer-container ul li a:hover {
          text-decoration: underline; }

footer .footer-bottom {
  background: #222;
  padding: 20px 0; }
  footer .footer-bottom .left,
  footer .footer-bottom .right {
    font-size: 12px;
    color: #aaa; }
  footer .footer-bottom .right {
    text-align: right; }
    footer .footer-bottom .right a {
      color: #aaa; }
      footer .footer-bottom .right a:hover {
        text-decoration: underline; }
