html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif; }

* {
  box-sizing: border-box; }

ul li {
  list-style: none; }

a {
  text-decoration: none; }

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

.button-outlined {
  align-items: center;
  background-color: transparent;
  -webkit-appearance: none;
  vertical-align: middle;
  justify-content: center;
  border: 1px solid #eee; }

.button-blue-line {
  border: 2px solid #3d5b99;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #3d5b99;
  background: #fff;
  border-radius: 3px;
  font-family: 'Lato', sans-serif;
  justify-content: center;
  align-items: center; }

.button-red-solid {
  justify-content: center;
  align-items: center;
  border: 0;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  font-family: 'Lato', sans-serif;
  border: 3px solid #db443a;
  background: #db443a;
  border-radius: 3px;
  text-transform: uppercase; }

.button-blue-solid {
  border: 3px solid #3d5b99;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  background: #3d5b99;
  border-radius: 3px;
  font-family: 'Lato', sans-serif;
  justify-content: center;
  align-items: center;
  text-transform: uppercase; }
  .button-blue-solid:hover {
    background-color: #3f61a9;
    border-color: #3f61a9; }

@media (min-width: 992px) {
  .container {
    max-width: 960px; } }

@media (min-width: 1200px) {
  .container {
    max-width: 1450px; } }

header .dropdown-menu {
  font-display: "Lato", sans-serif;
  background: #fff;
  padding: 10px 0;
  position: absolute;
  border-top: 4px solid #db443a;
  border-radius: 0;
  -webkit-box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.33);
  -moz-box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.33);
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.33); }
  header .dropdown-menu:after {
    bottom: 100%;
    border: solid transparent;
    content: ' ';
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-bottom-color: #db443a;
    border-width: 14px; }

